/*    */
.news-list-block {
    box-sizing: border-box;
    padding-bottom: 3rem;
    max-width: 1440px;
    /*   width  max-width */
    width: 100%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

/*  -    */
@media screen and (max-width: 375px) {
    .news-list-block {
        padding-left: 1rem;
        padding-right: 1rem;
    }
}

/*    */
.category-buttons {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: center;
    margin-bottom: 2rem;
}

/*     */
.category-btn {
    width: 95px;
    height: 35px;
    border: 1px solid #6C5242;
    background-color: transparent;
    border-radius: 8px;
    font-size: 18px;
    font-weight: 500;
    color: #6C5242;
    font-family: "Hahmlet", serif;
    text-align: center;
    line-height: 15px;
    cursor: pointer;

    margin-bottom: 10px;
}

/*  (active)  */
.category-btn.active {
    background-color: #6C5242;
    color: #ffffff;
}

/*   2  */
.news-list-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    /*   */
    gap: 1.5rem;
    width: 100%;
}

/*  1 */
@media screen and (max-width: 375px) {
    .news-list-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}