#loginBox {
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    pointer-events: none;
}

/*  */
#bgShadow {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0);
    opacity: 0;
    z-index: 1001;
    transition: opacity 0.3s ease, background-color 0.3s ease;
}

/*   */
#loginMain {
    width: 500px;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 1002;
    background-color: #FFFEF8;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: -5px 0 20px #444;
    transform: translateX(105%);
    transition: transform 0.5s ease;
}

/*   */
.lgclosebtn {
    width: 30px;
    color: #6C5242;
    position: absolute;
    top: 50px;
    right: 50px;
    cursor: pointer;
}

/* , ,      */
.lgtitlespan {
    position: absolute;
    top: 150px;
    font-size: 50px;
    font-weight: 500;
    color: #6C5242;
}

.lgspan1 {
    position: absolute;
    top: 230px;
    font-size: 16px;
    font-weight: 500;
    color: #6C5242;
}

.lgform {
    position: absolute;
    top: 300px;
    display: flex;
    flex-direction: column;
}

.lgidbox,
.lgpwbox {
    width: 280px;
    height: 30px;
    border: 1px solid #bbb;
    padding: 10px 15px;
    font-size: 18px;
    font-family: "Hahmlet", serif;
    background-color: #fff;
    margin-bottom: 20px;
}

.lgidbox:focus,
.lgpwbox:focus {
    border: 1px solid #6C5242;
}

.lgpwspan {
    color: #6C5242;
    text-decoration: underline;
    font-weight: 500;
    cursor: pointer;
}

.lgloginbtn,
.lgmembtn {
    width: 315px;
    height: 50px;
    font-size: 20px;
    font-family: "Hahmlet", serif;
    font-optical-sizing: auto;
    font-weight: 500;
    cursor: pointer;
    margin-top: 20px;
}

.lgloginbtn {
    background-color: #6C5242;
    color: #FFFEF8;
    margin-top: 10px;
}

.lgmembtn {
    background-color: #FFFEF8;
    border: 1px solid #6C5242;
    color: #6C5242;
}

/* , SNS      */
.lgline {
    width: 311px;
    height: 1px;
    background-color: #bbb;
    position: absolute;
    top: 352px;
    right: 0;
}

/*   */
#loginBox.active {
    pointer-events: auto;
    /*   */
}

#loginBox.active #loginMain {
    transform: translateX(0);
}

#loginBox.active #bgShadow {
    background-color: rgba(0, 0, 0, 0.3);
    opacity: 1;
}

.gsi-material-button {
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -webkit-appearance: none;
    background-color: #f2f2f2;
    background-image: none;
    border: none;
    -webkit-border-radius: 50%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    color: #1f1f1f;
    cursor: pointer;
    font-family: 'Roboto', arial, sans-serif;
    font-size: 14px;
    height: 75px;
    letter-spacing: 0.25px;
    outline: none;
    overflow: hidden;
    padding: 0;
    position: relative;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    width: 75px;
    min-width: min-content;
}

.gsi-material-button .gsi-material-button-icon {
    height: 60px;
    width: 60px;
    margin: 0;
}

.gsi-material-button .gsi-material-button-content-wrapper {
    -webkit-align-items: center;
    align-items: center;
    display: flex;
    -webkit-flex-direction: row;
    flex-direction: row;
    -webkit-flex-wrap: nowrap;
    flex-wrap: nowrap;
    height: 100%;
    justify-content: center;
    position: relative;
    width: 100%;
}

.gsi-material-button .gsi-material-button-contents {
    -webkit-flex-grow: 1;
    flex-grow: 1;
    font-family: 'Roboto', arial, sans-serif;
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: top;
}

.gsi-material-button .gsi-material-button-state {
    bottom: 0;
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
}

.gsi-material-button:disabled {
    cursor: default;
    background-color: #ffffff61;
}

.gsi-material-button:disabled .gsi-material-button-state {
    background-color: #1f1f1f1f;
}

.gsi-material-button:disabled .gsi-material-button-contents {
    opacity: 38%;
}

.gsi-material-button:disabled .gsi-material-button-icon {
    opacity: 38%;
}

.gsi-material-button:not(:disabled):active .gsi-material-button-state,
.gsi-material-button:not(:disabled):focus .gsi-material-button-state {
    background-color: #001d35;
    opacity: 12%;
}



/*   */
.after-login {
    position: absolute;
    top: 300px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    /* -      */
    width: 318px;
    height: 318px;
    /*    */
    box-sizing: border-box;
    gap: 0;
    /* justify-content   */
}

/*    */
.welcome-box {
    width: 100%;
    flex: 1;
    /*     */
    display: flex;
    align-items: center;
    /*    */
    justify-content: center;
    /*    */
    background-color: #FFFEF8;
    text-align: center;
    font-size: 18px;
    font-weight: 500;
    color: #6C5242;
    box-sizing: border-box;
    padding: 0 10px;
    /*   */
}

/*   */
.button-box {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 5px;
    /*     */
    /*     padding: 0 10px 10px; */
    box-sizing: border-box;
    justify-content: flex-end;
    /*    */
}

/*   */
.lgloginbtn,
.lgmembtn {
    width: 100%;
    height: 50px;
    font-size: 20px;
    font-family: "Hahmlet", serif;
    font-weight: 500;
    cursor: pointer;
    box-sizing: border-box;
}

/*   */
.lgloginbtn {
    background-color: #6C5242;
    color: #FFFEF8;
    border: none;
}

/*   */
.lgmembtn {
    background-color: #FFFEF8;
    border: 1px solid #6C5242;
    color: #6C5242;

}

@media (max-width: 375px) {
    #loginMain {
        width: 250vw;
        height: 120vh;
        position: absolute;
        top: 130vh;
        left: 150vw;
        border-radius: 3%;
        box-shadow: 0 0 10px #1f1f1f;
    }

    #bgShadow {
        width: 400vw;
        height: 400vh;
    }

    #loginBox.active #loginMain {
        left: 70vw;
    }
}