.news {
    width: 100%;
    /* height: 712px;  <-  */
    margin-bottom: 100px;
    /*    */
}

.news-header {
    text-align: center;
    margin-bottom: 110px;
}

.news-header h1 {
    color: #6C5242;
    margin-bottom: 5px;
    /* h1     */
}

.news_url {
    color: #6C5242;
    font-size: 14px;
    font-weight: 500;
    width: 290px;
    margin: 0 auto;
}

.news_url>a {
    text-decoration: underline;
}

.news_url>a:hover {
    background-color: #6C5242;
    color: #FFFEF8;
}

.newscon {
    width: 1440px;
    margin: 0 auto;
}