.custom-banner {
    width: 100%;
    height: 300px;
    margin-top: -30px;
    margin-bottom: 80px;
}

.weather-info {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 1200px;
    color: #FFFEF8;
    font-weight: 400;
    font-size: 16px;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
}

.weather-info>h2 {
    font-size: 35px;
    font-weight: 600;
    margin-bottom: 30px;
    text-align: center;
}

.wipbox {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}