#bannerMP {
    position: relative;
    width: 100%;
    height: 670px;
    background-color: #FFFEF8;
    overflow: hidden;
    cursor: pointer;
}

#bannerMP>img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

#bannerMP>img.active {
    opacity: 1;
}

#bannerbtnbox {
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    gap: 10px;
}

/*  :  */
#bannerbtnbox>.progress {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #bbb;
    border: none;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease;
    /*    */
}

/*   :  */
#bannerbtnbox>.progress.active {
    width: 50px;
    border-radius: 6px;
    /*   */
    background-color: #bbb;
}

/*   */
#bannerbtnbox>.progress::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    background-color: white;
    border-radius: 5px;
}

/*     */
#bannerbtnbox>.progress.active::before {
    animation: fillBar 4.3s linear forwards;
}

/*   */
@keyframes fillBar {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}